import {debounce} from '@/common/utils.js';
export default {
	data() {
		return {
			sectir: [],
			cloud: '',
			trye: '',
			cesrg: '',
			number: '',
			shade:false,
			flag: false,
			number2: '',
			lerst:'',
			stus: '',
			cancl:'取消',
			greed:'',
			scrollTop: 0,
			old: {
				scrollTop: 0,
			},
			hashrateTotal: 0,
			logsr: '0'
		};
	},
	onShow() {
		let that = this
		uni.request({
			url: that.url + 'cloud/dealersales/',
			method: 'GET',
			header: {
				Authorization: 'JWT' + ' ' + that.global_.token
			},
			success(res) {
				console.log(res)
				console.log(res.data.data.user_hashrate)
				if(res.data.data.user_hashrate==''){
					console.log('ccc')
					that.flag=true
				}else{
					that.sectir = res.data.data.user_hashrate
					that.logsr = res.data.data.total_hashrate
				}
			}
		})
	},
	methods: {

		scroll(e) {
			this.old.scrollTop = e.detail.scrollTop
		},
		backFront() {
			uni.navigateBack({
				delta: 1
			})
		},
		linkToTransfer1: debounce(
			function() {
				uni.navigateTo({
					url: '../../my/cunli_record/cunli_record'
				});
			},
			500,
			true
		),
		jilu: function() {
			this.linkToTransfer1();
		},
		btn: function(item) {
		    this.linkToTransfer(item);
		},
		linkToTransfer: debounce(
			function(item) {
				var that = this
				uni.request({
					url:this.url + 'cloudbuys',
					method:'GET',
					header:{
						Authorization: 'JWT' + ' ' + that.global_.token
					},
					success(res) {
						console.log(item)
						that.cloud = item.id
						that.number = item.hashrate
						console.log(that.number)
						if (res.statusCode == 200) {
							uni.navigateTo({
								url: '../../my/posjfig/posjfig?aa=' + that.cloud + '&ff=' + that.number
							})
						}
						if (res.statusCode == 400) {
							uni.showToast({
								title:'用户未实名认证或未设置资金密码',
								icon:'none'
							})
						}
					}
				})
			},
			1000,
			true
		),
		btn2: function(item) {
		    this.linkToTransfer2(item);
		},
		linkToTransfer2: debounce(
			function(item) {
				var that = this
				uni.request({
					url:this.url + 'cloudbuys',
					method:'GET',
					header:{
						Authorization: 'JWT' + ' ' + that.global_.token
					},
					success(res) {
						if (res.statusCode == 200) {
							that.trye = item.id
							that.number2 = item.hashrate
							uni.navigateTo({
								url: '../../my/poder/poder?ss=' + that.trye + '&cc=' + that.number2
							})
						}
						if (res.statusCode == 400) {
							uni.showToast({
								title:'用户未实名认证或未设置资金密码',
								icon:'none'
							})
						}
					}
				})
			},
			1000,
			true
		),
	
	}
};
